// src/components/auth/AuthModal.tsx
// „É≠„Ç∞„Ç§„É≥„Éª„Çµ„Ç§„É≥„Ç¢„ÉÉ„Éó„É¢„Éº„ÉÄ„É´UI
// Êó¢Â≠ò„Éá„Ç∂„Ç§„É≥„Ç∑„Çπ„ÉÜ„É†Ê∫ñÊã†„ÉªCOPPAÂØæÂøú

import React, { useState, useEffect } from 'react'
import { useAuth } from '../../hooks/useAuth'

interface AuthModalProps {
  isOpen: boolean
  onClose: () => void
  defaultMode?: 'signin' | 'signup'
}

type AuthMode = 'signin' | 'signup' | 'reset'

const AuthModal: React.FC<AuthModalProps> = ({ 
  isOpen, 
  onClose, 
  defaultMode = 'signin' 
}) => {
  const { signIn, signUp, resetPassword, loading, error, clearError } = useAuth()
  
  const [mode, setMode] = useState<AuthMode>(defaultMode)
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    username: '',
    displayName: '',
    age: '',
    language: 'ja'
  })
  
  const [validationErrors, setValidationErrors] = useState<Record<string, string>>({})
  const [showPassword, setShowPassword] = useState(false)
  const [showParentalWarning, setShowParentalWarning] = useState(false)

  // „É¢„Éº„ÉÄ„É´ÈñãÈñâÊôÇ„ÅÆ„É™„Çª„ÉÉ„Éà
  useEffect(() => {
    if (isOpen) {
      setMode(defaultMode)
      setFormData({
        email: '',
        password: '',
        confirmPassword: '',
        username: '',
        displayName: '',
        age: '',
        language: 'ja'
      })
      setValidationErrors({})
      clearError()
    }
  }, [isOpen, defaultMode, clearError])

  // Esc„Ç≠„Éº„Åß„É¢„Éº„ÉÄ„É´„ÇíÈñâ„Åò„Çã
  useEffect(() => {
    const handleEscape = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isOpen) {
        onClose()
      }
    }
    
    if (isOpen) {
      document.addEventListener('keydown', handleEscape)
      document.body.style.overflow = 'hidden'
    }
    
    return () => {
      document.removeEventListener('keydown', handleEscape)
      document.body.style.overflow = 'unset'
    }
  }, [isOpen, onClose])

  // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥
  const validateForm = (): boolean => {
    const errors: Record<string, string> = {}
    
    if (!formData.email.trim()) {
      errors.email = '„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      errors.email = '„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅÆÂΩ¢Âºè„ÅåÊ≠£„Åó„Åè„ÅÇ„Çä„Åæ„Åõ„Çì'
    }
    
    if (mode !== 'reset') {
      if (!formData.password.trim()) {
        errors.password = '„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
      } else if (formData.password.length < 6) {
        errors.password = '„Éë„Çπ„ÉØ„Éº„Éâ„ÅØ6ÊñáÂ≠ó‰ª•‰∏ä„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô'
      }
    }
    
    if (mode === 'signup') {
      if (formData.password !== formData.confirmPassword) {
        errors.confirmPassword = '„Éë„Çπ„ÉØ„Éº„Éâ„Åå‰∏ÄËá¥„Åó„Åæ„Åõ„Çì'
      }
      
      if (!formData.username.trim()) {
        errors.username = '„É¶„Éº„Ç∂„ÉºÂêç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
      } else if (formData.username.length < 3) {
        errors.username = '„É¶„Éº„Ç∂„ÉºÂêç„ÅØ3ÊñáÂ≠ó‰ª•‰∏ä„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô'
      } else if (!/^[a-zA-Z0-9_]+$/.test(formData.username)) {
        errors.username = '„É¶„Éº„Ç∂„ÉºÂêç„ÅØËã±Êï∞Â≠ó„Å®„Ç¢„É≥„ÉÄ„Éº„Çπ„Ç≥„Ç¢„ÅÆ„Åø‰ΩøÁî®„Åß„Åç„Åæ„Åô'
      }
      
      const age = parseInt(formData.age)
      if (!formData.age.trim() || isNaN(age)) {
        errors.age = 'Âπ¥ÈΩ¢„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
      } else if (age < 6 || age > 100) {
        errors.age = 'Âπ¥ÈΩ¢„ÅØ6Ê≠≥‰ª•‰∏ä100Ê≠≥‰ª•‰∏ã„ÅßÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
      }
    }
    
    setValidationErrors(errors)
    return Object.keys(errors).length === 0
  }

  // „Éï„Ç©„Éº„É†ÈÄÅ‰ø°Âá¶ÁêÜ
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return
    
    try {
      if (mode === 'signin') {
        await signIn(formData.email, formData.password)
        onClose()
      } else if (mode === 'signup') {
        const age = parseInt(formData.age)
        
        // 13Ê≠≥Êú™Ê∫Ä„ÅÆÂ†¥Âêà„ÅØ‰øùË≠∑ËÄÖÂêåÊÑè„ÅÆË≠¶Âëä„ÇíË°®Á§∫
        if (age < 13 && !showParentalWarning) {
          setShowParentalWarning(true)
          return
        }
        
        await signUp(formData.email, formData.password, {
          username: formData.username,
          displayName: formData.displayName || formData.username,
          age,
          language: formData.language
        })
        onClose()
      } else if (mode === 'reset') {
        await resetPassword(formData.email)
        alert('„Éë„Çπ„ÉØ„Éº„Éâ„É™„Çª„ÉÉ„Éà„É°„Éº„É´„ÇíÈÄÅ‰ø°„Åó„Åæ„Åó„Åü„ÄÇ„É°„Éº„É´„Çí„ÅîÁ¢∫Ë™ç„Åè„Å†„Åï„ÅÑ„ÄÇ')
        setMode('signin')
      }
    } catch (error) {
      // „Ç®„É©„Éº„ÅØuseAuth„ÅßÁÆ°ÁêÜ„Åï„Çå„Çã„Åü„ÇÅ„ÄÅ„Åì„Åì„Åß„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
      console.error('Auth error:', error)
    }
  }

  // ÂÖ•ÂäõÂ§âÊõ¥Âá¶ÁêÜ
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
    
    // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥„Ç®„É©„Éº„Çí„ÇØ„É™„Ç¢
    if (validationErrors[name]) {
      setValidationErrors(prev => ({ ...prev, [name]: '' }))
    }
  }

  // „É¢„Éº„ÉÄ„É´„ÅåÈñâ„Åò„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØ‰Ωï„ÇÇË°®Á§∫„Åó„Å™„ÅÑ
  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      {/* „Ç™„Éº„Éê„Éº„É¨„Ç§ */}
      <div 
        className="fixed inset-0 bg-black bg-opacity-50 transition-opacity"
        onClick={onClose}
      />
      
      {/* „É¢„Éº„ÉÄ„É´Êú¨‰Ωì */}
      <div className="flex min-h-full items-center justify-center p-4">
        <div 
          className="relative bg-white rounded-2xl shadow-2xl w-full max-w-md p-6 transform transition-all"
          onClick={(e) => e.stopPropagation()}
        >
          {/* Èñâ„Åò„Çã„Éú„Çø„É≥ */}
          <button
            onClick={onClose}
            className="absolute top-4 right-4 text-gray-400 hover:text-gray-600 transition-colors"
            disabled={loading}
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>

          {/* „Éò„ÉÉ„ÉÄ„Éº */}
          <div className="text-center mb-6">
            <div className="w-16 h-16 mx-auto mb-4 bg-gradient-to-r from-pink-500 to-purple-600 rounded-full flex items-center justify-center">
              <span className="text-2xl">üéÆ</span>
            </div>
            <h2 className="text-2xl font-bold text-gray-900">
              {mode === 'signin' && '„É≠„Ç∞„Ç§„É≥'}
              {mode === 'signup' && '„Ç¢„Ç´„Ç¶„É≥„Éà‰ΩúÊàê'}
              {mode === 'reset' && '„Éë„Çπ„ÉØ„Éº„Éâ„É™„Çª„ÉÉ„Éà'}
            </h2>
            <p className="text-gray-600 mt-2">
              {mode === 'signin' && '„Ç≤„Éº„É†‰ΩúÊàê„ÇíÂßã„ÇÅ„Åæ„Åó„Çá„ÅÜÔºÅ'}
              {mode === 'signup' && 'Êñ∞„Åó„ÅÑ„Ç¢„Ç´„Ç¶„É≥„Éà„Çí‰ΩúÊàê'}
              {mode === 'reset' && '„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ'}
            </p>
          </div>

          {/* „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏ */}
          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-xl text-red-700 text-sm">
              {error}
            </div>
          )}

          {/* 13Ê≠≥Êú™Ê∫ÄË≠¶Âëä */}
          {showParentalWarning && (
            <div className="mb-4 p-4 bg-amber-50 border border-amber-200 rounded-xl">
              <h3 className="font-semibold text-amber-800 mb-2">‚ö†Ô∏è ‰øùË≠∑ËÄÖ„ÅÆÂêåÊÑè„Å´„Å§„ÅÑ„Å¶</h3>
              <p className="text-amber-700 text-sm mb-3">
                13Ê≠≥Êú™Ê∫Ä„ÅÆ„ÅäÂ≠êÊßò„Å´„ÅØ‰øùË≠∑ËÄÖ„ÅÆÂêåÊÑè„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ‰øùË≠∑ËÄÖ„ÅÆÊñπ„Å®‰∏ÄÁ∑í„Å´„Ç¢„Ç´„Ç¶„É≥„Éà„Çí‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ
              </p>
              <div className="flex gap-2">
                <button
                  type="button"
                  onClick={() => setShowParentalWarning(false)}
                  className="px-3 py-1 bg-white border border-amber-300 text-amber-700 rounded-lg text-sm"
                >
                  „Ç≠„É£„É≥„Çª„É´
                </button>
                <button
                  type="button"
                  onClick={() => {
                    setShowParentalWarning(false)
                    handleSubmit({ preventDefault: () => {} } as React.FormEvent)
                  }}
                  className="px-3 py-1 bg-amber-600 text-white rounded-lg text-sm"
                >
                  ÂêåÊÑè„Åó„Å¶Á∂ö„Åë„Çã
                </button>
              </div>
            </div>
          )}

          {/* „Éï„Ç©„Éº„É† */}
          <form onSubmit={handleSubmit} className="space-y-4">
            {/* „É°„Éº„É´„Ç¢„Éâ„É¨„Çπ */}
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-1">
                „É°„Éº„É´„Ç¢„Éâ„É¨„Çπ
              </label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleInputChange}
                className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors ${
                  validationErrors.email ? 'border-red-300 bg-red-50' : 'border-gray-300'
                }`}
                placeholder="your@example.com"
                disabled={loading}
              />
              {validationErrors.email && (
                <p className="mt-1 text-sm text-red-600">{validationErrors.email}</p>
              )}
            </div>

            {/* „Éë„Çπ„ÉØ„Éº„Éâ */}
            {mode !== 'reset' && (
              <div>
                <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-1">
                  „Éë„Çπ„ÉØ„Éº„Éâ
                </label>
                <div className="relative">
                  <input
                    type={showPassword ? 'text' : 'password'}
                    id="password"
                    name="password"
                    value={formData.password}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 pr-12 border rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors ${
                      validationErrors.password ? 'border-red-300 bg-red-50' : 'border-gray-300'
                    }`}
                    placeholder="6ÊñáÂ≠ó‰ª•‰∏ä"
                    disabled={loading}
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                    disabled={loading}
                  >
                    {showPassword ? 'üôà' : 'üëÅÔ∏è'}
                  </button>
                </div>
                {validationErrors.password && (
                  <p className="mt-1 text-sm text-red-600">{validationErrors.password}</p>
                )}
              </div>
            )}

            {/* „Çµ„Ç§„É≥„Ç¢„ÉÉ„ÉóËøΩÂä†„Éï„Ç£„Éº„É´„Éâ */}
            {mode === 'signup' && (
              <>
                {/* „Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç */}
                <div>
                  <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700 mb-1">
                    „Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç
                  </label>
                  <input
                    type={showPassword ? 'text' : 'password'}
                    id="confirmPassword"
                    name="confirmPassword"
                    value={formData.confirmPassword}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors ${
                      validationErrors.confirmPassword ? 'border-red-300 bg-red-50' : 'border-gray-300'
                    }`}
                    placeholder="„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÜçÂÖ•Âäõ"
                    disabled={loading}
                  />
                  {validationErrors.confirmPassword && (
                    <p className="mt-1 text-sm text-red-600">{validationErrors.confirmPassword}</p>
                  )}
                </div>

                {/* „É¶„Éº„Ç∂„ÉºÂêç */}
                <div>
                  <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-1">
                    „É¶„Éº„Ç∂„ÉºÂêç
                  </label>
                  <input
                    type="text"
                    id="username"
                    name="username"
                    value={formData.username}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors ${
                      validationErrors.username ? 'border-red-300 bg-red-50' : 'border-gray-300'
                    }`}
                    placeholder="ÂçäËßíËã±Êï∞Â≠ó„Å®„Ç¢„É≥„ÉÄ„Éº„Çπ„Ç≥„Ç¢"
                    disabled={loading}
                  />
                  {validationErrors.username && (
                    <p className="mt-1 text-sm text-red-600">{validationErrors.username}</p>
                  )}
                </div>

                {/* Ë°®Á§∫Âêç */}
                <div>
                  <label htmlFor="displayName" className="block text-sm font-medium text-gray-700 mb-1">
                    Ë°®Á§∫Âêç <span className="text-gray-400 text-xs">(‰ªªÊÑè)</span>
                  </label>
                  <input
                    type="text"
                    id="displayName"
                    name="displayName"
                    value={formData.displayName}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors"
                    placeholder="„Åø„Çì„Å™„Å´Ë°®Á§∫„Åï„Çå„ÇãÂêçÂâç"
                    disabled={loading}
                  />
                </div>

                {/* Âπ¥ÈΩ¢„ÉªË®ÄË™û */}
                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <label htmlFor="age" className="block text-sm font-medium text-gray-700 mb-1">
                      Âπ¥ÈΩ¢
                    </label>
                    <input
                      type="number"
                      id="age"
                      name="age"
                      value={formData.age}
                      onChange={handleInputChange}
                      className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors ${
                        validationErrors.age ? 'border-red-300 bg-red-50' : 'border-gray-300'
                      }`}
                      placeholder="15"
                      min="6"
                      max="100"
                      disabled={loading}
                    />
                    {validationErrors.age && (
                      <p className="mt-1 text-sm text-red-600">{validationErrors.age}</p>
                    )}
                  </div>
                  
                  <div>
                    <label htmlFor="language" className="block text-sm font-medium text-gray-700 mb-1">
                      Ë®ÄË™û
                    </label>
                    <select
                      id="language"
                      name="language"
                      value={formData.language}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-pink-500 focus:border-pink-500 transition-colors"
                      disabled={loading}
                    >
                      <option value="ja">Êó•Êú¨Ë™û</option>
                      <option value="en">English</option>
                      <option value="ko">ÌïúÍµ≠Ïñ¥</option>
                      <option value="zh">‰∏≠Êñá</option>
                    </select>
                  </div>
                </div>
              </>
            )}

            {/* ÈÄÅ‰ø°„Éú„Çø„É≥ */}
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-gradient-to-r from-pink-500 to-purple-600 text-white py-3 rounded-xl font-semibold hover:from-pink-600 hover:to-purple-700 focus:ring-4 focus:ring-pink-200 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Âá¶ÁêÜ‰∏≠...
                </span>
              ) : (
                <>
                  {mode === 'signin' && '„É≠„Ç∞„Ç§„É≥'}
                  {mode === 'signup' && '„Ç¢„Ç´„Ç¶„É≥„Éà‰ΩúÊàê'}
                  {mode === 'reset' && '„É™„Çª„ÉÉ„Éà„É°„Éº„É´ÈÄÅ‰ø°'}
                </>
              )}
            </button>
          </form>

          {/* „Éï„ÉÉ„Çø„Éº„É™„É≥„ÇØ */}
          <div className="mt-6 text-center text-sm">
            {mode === 'signin' && (
              <>
                <p className="text-gray-600">
                  „Åæ„Å†„Ç¢„Ç´„Ç¶„É≥„Éà„Çí„ÅäÊåÅ„Å°„Åß„Å™„ÅÑÊñπ„ÅØ{' '}
                  <button
                    type="button"
                    onClick={() => setMode('signup')}
                    className="text-pink-600 hover:text-pink-700 font-medium"
                    disabled={loading}
                  >
                    „Ç¢„Ç´„Ç¶„É≥„Éà‰ΩúÊàê
                  </button>
                </p>
                <p className="mt-2">
                  <button
                    type="button"
                    onClick={() => setMode('reset')}
                    className="text-gray-500 hover:text-gray-700"
                    disabled={loading}
                  >
                    „Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂøò„Çå„ÅüÊñπ
                  </button>
                </p>
              </>
            )}
            
            {mode === 'signup' && (
              <p className="text-gray-600">
                Êó¢„Å´„Ç¢„Ç´„Ç¶„É≥„Éà„Çí„ÅäÊåÅ„Å°„ÅÆÊñπ„ÅØ{' '}
                <button
                  type="button"
                  onClick={() => setMode('signin')}
                  className="text-pink-600 hover:text-pink-700 font-medium"
                  disabled={loading}
                >
                  „É≠„Ç∞„Ç§„É≥
                </button>
              </p>
            )}
            
            {mode === 'reset' && (
              <p className="text-gray-600">
                <button
                  type="button"
                  onClick={() => setMode('signin')}
                  className="text-pink-600 hover:text-pink-700 font-medium"
                  disabled={loading}
                >
                  „É≠„Ç∞„Ç§„É≥„Å´Êàª„Çã
                </button>
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default AuthModal